import React from 'react';
import ReactDOM from 'react-dom';
import * as twitchbot from './twitchbot.js'; 
import './css/main.css';

class Title extends React.Component {
  render() {
    return (
      <h1 className="title">Dungeon Jam
      <span className="subtext">ver 0.0.1</span>
      </h1>
    );
  }
}

class Player extends React.Component{
  render(){
    return(
      <div className="player"></div>
    );
  }
}

class Narrator extends React.Component{
  render(){
    return(
      <div className="narrator-box"></div>
    );
  }
}


class Map extends React.Component{
  renderTile = (i) => {
    return <Tile />;
  }

  constructor(props)
  {
    super(props);
    this.handleNorth = this.handleNorth.bind(this);
    this.handleSouth = this.handleSouth.bind(this);
    this.handleEast = this.handleEast.bind(this);
    this.handleWest = this.handleWest.bind(this);

    this.state = {
        x: 0,
        y: 0,
    };
  }

    handleNorth(event){
      this.setState({x: event.target.value});
    }
    handleSouth(event){
      this.setState({x:event.target.value});
    }
     handleEast(event){
      this.setState({x: event.target.value});
    }
    handleWest(event){
      this.setState({x:event.target.value});
    }

    updateCoord = event => {
    event.preventDefault();
    this.setState({
      x: event.target.value,
      y: event.target.value,
    });
  }
  
  

  loadMap = () => {
  let tiles = []

    for (let i = 0; i < 400; ++i)
    {
     tiles.push(<li>{this.renderTile(i)}</li>)
    }
    return tiles
  }

  render(){
    return(
      <ul className="map">
      {this.loadMap()}
      </ul>
    )
  }
}

class Tile extends React.Component{
  render(){
    return(
      <div className="tile"></div>
    )
  }
}


class Game extends React.Component {
  render() {
    return (
      <div className="game-ui">
      <Title />
      <Player />
      <Map />
      <Narrator />
      </div>
    );
  }
}

// ========================================

ReactDOM.render(
  <Game />,
  document.getElementById('root')
);



